	module.exports = {"data":{"site":{"siteMetadata":{"title":"Psyked.co.uk","author":"James Ford"}},"markdownRemark":{"id":"/Users/James/Repositories/portfolio-2018/src/posts/feeling-smart-try-googles-code-jam/index.md absPath of file >>> MarkdownRemark","html":"<p><a href=\"http://code.google.com/codejam/\"><img src=\"http://uploads.psyked.co.uk/2008/07/codejam.jpg\" title=\"codejam\"></a></p>\n<p>Google Code Jam is a pretty much a coding competition.  You can use any programming language you like, they'll give you a problem, and you have to solve it.  Sound fun?  Well, you have between four and eight minutes to submit your answers, and you'll get three to six problems per round.  Still sound like fun?</p>\n<p>Well, the 24 hr Qualification Round Begins on Wednesday July 16th, and you can <a href=\"http://code.google.com/codejam/contest/registration\">register here</a>.</p>\n<p>I think I'm going to attempt the qualification round, but I have no illusions about the accuracy of my speed-coding.  The guys at Google wouldn't make things too difficult - would they?</p>","frontmatter":{"title":"Feeling smart? Try Google's Code Jam","date":"July 01, 2008","tags":["code-jam","competitions","google"]}}},"pathContext":{}}

